import React, { useEffect, useState } from "react";

const MediaRenderer = ({ url, onClick, className }) => {
  const [isImage, setIsImage] = useState(null); // null = loading, true = image, false = video

  useEffect(() => {
    if (!url) return;

    const img = new Image();
    img.onload = () => setIsImage(true);
    img.onerror = () => setIsImage(false);
    img.src = url;
  }, [url]);

  if (isImage === null) return <p></p>;

  return (
    <>
      {isImage ? (
        <img
          src={url}
          alt="media"
          className={className}
          style={{ maxWidth: "100%" }}
          onClick={onClick}
        />
      ) : (
        <video controls style={{ maxWidth: "100%" }} onClick={onClick}>
          <source src={url} />
          Your browser does not support the video tag.
        </video>
      )}
    </>
  );
};

export default MediaRenderer;
