import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import pe1 from "../assets/images/pic.png";
import axios from "axios";
import icon2 from "../assets/images/bell.png";
import { Usergallerynotification } from "../components/Usergallerynotification";
import { useTranslation } from "react-i18next";
import i18n from "../i18n"; // Make sure i18n is imported properly
function Notification({ activeMenu, toggleMenu, refresh }) {
  var apiUrlnoti = "https://backend.amourette.no/api/notifications/";
  var apiUrl = "https://backend.amourette.no/api/members/";
  const [AllfriendId, setAllfriendId] = useState([]);
  const [totalcount, settotalcount] = useState("");
  const [notificationsdetail, setnotificationsdetail] = useState([]);
  const [notificationsdetailtest, setnotificationsdetailtest] = useState([]);
  const [LoginData, setLoginData] = useState("");

  const [page, setPage] = useState(1);
  useEffect(() => {
    const interval = setInterval(() => {
      getAllfriends();
    }, 2000); // Runs every 2 seconds

    return () => clearInterval(interval); // Cleanup on unmount
  }, []);
  const getAllfriends = async () => {
    const storedUsername = localStorage.getItem("userLogin");
    const userLogin = storedUsername ? JSON.parse(storedUsername) : null;
    let formData = {
      user_id: userLogin.id,
    };
    try {
      const res = await axios.post(apiUrl + "getAllfriends", formData, {
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json", // Ensure the content type is JSON
        },
      });
      var respo = res.data.results;
      const friendUserIds = respo.map((friend) => friend.id);
      setAllfriendId(friendUserIds);

      //const galleries = await getnotifications(friendUserIds);
      await getnotificationsall(friendUserIds);
    } catch (err) {
      // Enhanced error handling
      if (err.response) {
      } else if (err.request) {
        console.error("Request data:", err.request);
      } else {
        console.error("Error message:", err.message);
      }
    }
  };

  const getnotifications = async (userIds, pageNumber = 1) => {
    const storedUsername = localStorage.getItem("userLogin");
    const userLogin = storedUsername ? JSON.parse(storedUsername) : null;

    const userIdsWithSelf = [...userIds, userLogin.id]; // e.g., [3, 1, 5]
    const userIdsString = userIdsWithSelf.join(", ");
    let formData = {
      user_ids: userIdsString, // Sending as a string for the query
      user_id: userLogin.id,
      pageNumber: pageNumber,
    };
    try {
      const res = await axios.post(apiUrlnoti + "getnotifications", formData, {
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json", // Ensure the content type is JSON
        },
      });
      var respo = res.data.results;
      console.log(respo);
      if (pageNumber === 1) {
        setnotificationsdetail(respo); // First page, replace notifications
      } else {
        setnotificationsdetail((prev) => [...prev, ...respo]); // Append next 20 notifications
      }

      // Count unread notifications
    } catch (err) {
      // Enhanced error handling
      if (err.response) {
      } else if (err.request) {
        console.error("Request data:", err.request);
      } else {
        console.error("Error message:", err.message);
      }
    }
  };

  const getnotificationsall = async (friendUserIds) => {
    const storedUsername = localStorage.getItem("userLogin");
    const userLogin = storedUsername ? JSON.parse(storedUsername) : null;

    const userIdsWithSelf = [...friendUserIds, userLogin.id]; // e.g., [3, 1, 5]
    const userIdsString = userIdsWithSelf.join(", ");
    let formData = {
      user_ids: userIdsString, // Sending as a string for the query
      user_id: userLogin.id,
    };
    try {
      const res = await axios.post(
        apiUrlnoti + "getnotificationsall",
        formData,
        {
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json", // Ensure the content type is JSON
          },
        }
      );
      var respo = res.data.results;
      console.log(res.data.resultcount);
      var respocount = res.data.resultcount;
      setnotificationsdetailtest(respo);

      settotalcount(respocount);
    } catch (err) {
      // Enhanced error handling
      if (err.response) {
      } else if (err.request) {
        console.error("Request data:", err.request);
      } else {
        console.error("Error message:", err.message);
      }
    }
  };
  const getnotificationsallMessages = async () => {
    const storedUsername = localStorage.getItem("userLogin");
    const userLogin = storedUsername ? JSON.parse(storedUsername) : null;

    let formData = {
      user_id: userLogin.id,
    };
    try {
      const res = await axios.post(
        apiUrlnoti + "getnotificationsallMessages",
        formData,
        {
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json", // Ensure the content type is JSON
          },
        }
      );
      var respo = res.data.results;
      console.log(respo);
      //settotalcount(respo.length);
    } catch (err) {
      // Enhanced error handling
      if (err.response) {
      } else if (err.request) {
        console.error("Request data:", err.request);
      } else {
        console.error("Error message:", err.message);
      }
    }
  };
  const handleclicknotification = () => {
    const nextPage = page + 1; // Increase the page number
    setPage(nextPage); // Update the state
    getnotifications(AllfriendId, nextPage); // Fetch next 20 notifications
  };

  const formateTime = (dateString) => {
    if (!dateString) return null;

    // Parse the date as UTC
    const postDate = new Date(dateString);
    if (isNaN(postDate.getTime())) {
      console.error("Invalid date:", dateString);
      return "Invalid date";
    }

    // Get the current time in UTC
    const now = new Date();

    // Adjust the postDate by subtracting 1 hour (3600000ms)
    const adjustedPostDate = new Date(postDate.getTime() - 3600000);

    // Convert postDate to Europe/Oslo time correctly
    const osloPostTime = new Intl.DateTimeFormat("en-GB", {
      timeZone: "Europe/Oslo",
      hour: "2-digit",
      minute: "2-digit",
      hour12: false, // Ensures 24-hour format
    }).format(adjustedPostDate);

    // Calculate time difference in seconds, minutes, hours, days
    const timeDiff = now.getTime() - postDate.getTime();
    const seconds = Math.floor(timeDiff / 1000);
    const minutes = Math.floor(seconds / 60);
    const hours = Math.floor(minutes / 60);
    const days = Math.floor(hours / 24);
    const weeks = Math.floor(days / 7);
    const months = Math.floor(days / 30);
    const years = Math.floor(days / 365);

    // Generate formatted output based on the difference
    if (minutes < 60) return `${osloPostTime}`;
    if (hours < 24) return `${osloPostTime}`;
    if (days === 1) return `Yesterday at ${osloPostTime}`;
    if (days < 7) return `${days}d  ${osloPostTime}`;
    if (weeks < 4) return `${weeks}w  ${osloPostTime}`;
    if (months < 12) return `${months}m  ${osloPostTime}`;
    return `${years}y  ${osloPostTime}`;
  };
  //const [activeMenu, setActiveMenu] = useState(null);
  const handleNotificationToggle = async (menuId) => {
    //setActiveMenu(activeMenu === menuId ? null : menuId);
    getnotifications(AllfriendId);
    const storedUsername = localStorage.getItem("userLogin");
    const userLogin = storedUsername ? JSON.parse(storedUsername) : null;

    const userIdsWithSelf = [...AllfriendId, userLogin.id]; // e.g., [3, 1, 5]
    const userIdsString = userIdsWithSelf.join(", ");
    let formData = {
      user_ids: userIdsString, // Sending as a string for the query
      user_id: userLogin.id,
    };
    //console.log(formData);
    try {
      const res = await axios.post(
        apiUrlnoti + "updatenotifications",
        formData,
        {
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json", // Ensure the content type is JSON
          },
        }
      );
      var respo = res.data.results;

      getAllfriends();
    } catch (err) {
      // Enhanced error handling
      if (err.response) {
      } else if (err.request) {
        console.error("Request data:", err.request);
      } else {
        console.error("Error message:", err.message);
      }
    }
    toggleMenu(menuId);
  };
  const { t } = useTranslation();

  useEffect(() => {
    const savedLanguage = localStorage.getItem("language") || "en"; // Default to 'en' if no language is saved

    // Change the language if it's not already the current language
    if (i18n.language !== savedLanguage) {
      i18n.changeLanguage(savedLanguage);
    }
  }, [i18n, t]);
  //Gallery popup
  const [isPopupOpen1, setIsPopupOpen1] = useState(false);
  const [seperateGallerydetail, setseperateGallerydetail] = useState("");
  const [UserDetail, setUserDetail] = useState("");
  const [GalleryCommets, setGalleryCommets] = useState([]);
  const [GalleryIdDetailGet, setGalleryIdDetailGet] = useState("");
  const [nextId, setnextId] = useState("");
  const [previousId, setpreviousId] = useState("");
  const closePopup1 = () => {
    setIsPopupOpen1(false);
    document.body.style.overflowY = "auto";
  };
  var apiUrlMember = "https://backend.amourette.no/api/members/";
  useEffect(() => {
    const storedUsername = localStorage.getItem("userLogin");
    const userLogin = storedUsername ? JSON.parse(storedUsername) : null;
    setLoginData(userLogin);
    getUserDetail();
  }, []);
  const getUserDetail = async () => {
    const storedUsername = localStorage.getItem("userLogin");
    const userLogin = storedUsername ? JSON.parse(storedUsername) : null;
    let formData = {
      user_id: userLogin.id,
    };
    try {
      const res = await axios.post(apiUrlMember + "getUserDetail", formData, {
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json", // Ensure the content type is JSON
        },
      });
      if (res.data.row.length > 0) {
        var respo = res.data.row[0];
        setUserDetail(respo);
      }
    } catch (err) {
      // Enhanced error handling
      if (err.response) {
      } else if (err.request) {
        console.error("Request data:", err.request);
      } else {
        console.error("Error message:", err.message);
      }
    }
  };
  const getGalleryDetail = async (galleryId) => {
    console.log(";ll");
    setGalleryIdDetailGet(galleryId);
    getGalleryComments(galleryId);
    getGalleryDetailnextprevious(galleryId);
    let formData = {
      id: galleryId,
      user_id: LoginData.id,
    };
    try {
      const res = await axios.post(
        apiUrlMember + "getGalleryDetail",
        formData,
        {
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json", // Ensure the content type is JSON
          },
        }
      );
      if (res.data.row.length > 0) {
        var respo = res.data.row[0];
        openPopup1();
        setseperateGallerydetail(respo);
      }
    } catch (err) {
      // Enhanced error handling
      if (err.response) {
      } else if (err.request) {
        console.error("Request data:", err.request);
      } else {
        console.error("Error message:", err.message);
      }
    }
  };
  const getGalleryDetailnextprevious = async (galleryId) => {
    let formData = {
      id: galleryId,
      user_id: LoginData.id,
    };
    try {
      const res = await axios.post(
        apiUrlMember + "getGalleryDetailnextprevious",
        formData,
        {
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json", // Ensure the content type is JSON
          },
        }
      );

      if (res.data.next_gallery_id !== null) {
        setpreviousId(res.data.next_gallery_id);
      }
      if (res.data.previous_gallery_id !== null) {
        setnextId(res.data.previous_gallery_id);
      }
    } catch (err) {
      // Enhanced error handling
      if (err.response) {
      } else if (err.request) {
        console.error("Request data:", err.request);
      } else {
        console.error("Error message:", err.message);
      }
    }
  };
  const getUserfav = async () => {
    getGalleryDetail(GalleryIdDetailGet);
  };
  const getCommentPost = async () => {
    getGalleryDetail(GalleryIdDetailGet);
  };
  const getgallerydetialnext = () => {
    getGalleryDetail(nextId);
  };
  const getgallerydetialprev = () => {
    getGalleryDetail(previousId);
  };
  const getGalleryComments = async (g_id) => {
    let formData = {
      id: g_id,
      user_id: LoginData.id,
    };
    try {
      const res = await axios.post(apiUrl + "getGalleryComments", formData, {
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json", // Ensure the content type is JSON
        },
      });

      var respo = res.data.results;

      setGalleryCommets(respo);
    } catch (err) {
      // Enhanced error handling
      if (err.response) {
      } else if (err.request) {
        console.error("Request data:", err.request);
      } else {
        console.error("Error message:", err.message);
      }
    }
  };
  const openPopup1 = () => {
    setIsPopupOpen1(true);
    document.body.style.overflowY = "hidden";
  };

  //Gallery popup
  return (
    <>
      <div className="">
        <button
          type="button"
          className={`topmenu ${activeMenu === "menu2" ? "active" : ""}`}
          onClick={() => handleNotificationToggle("menu2")}
        >
          {totalcount > 0 && (
            <span className="notinum">
              {totalcount > 99 ? "99+" : totalcount}
            </span>
          )}

          <img src={icon2} alt="userp" />
        </button>
        <div
          className="menubox menuboxnotification"
          style={{
            display: activeMenu === "menu2" ? "block" : "none",
          }}
        >
          <div className="d-flex flex-column gap-4">
            <h3>{t("Notification")}</h3>
            {(Array.isArray(notificationsdetail) &&
              notificationsdetail.length === 2) ||
            notificationsdetail.length === 1 ||
            notificationsdetail.length === 3 ? (
              <div
                className="d-flex flex-column gap-3"
                style={{
                  maxHeight: "200px", // Fix height for 4 or more notifications
                  overflowY: "auto", // Enable scrolling
                }}
              >
                {notificationsdetail.length === 0 ? (
                  <p>{t("No_result_found")}</p>
                ) : (
                  <div className="d-flex flex-column gap-3 scroll_bar scrollyover">
                    {notificationsdetail.map((notification, index) => (
                      <Link
                        to={
                          notification.link_href?.trim() === "/previewimages" &&
                          notification.post_id &&
                          notification.image_url
                            ? `/previewimages/${
                                notification.post_id
                              }/${encodeURIComponent(notification.image_url)}`
                            : notification.link_href?.trim() || "#"
                        }
                        onClick={(e) => {
                          const trimmedHref = notification.link_href?.trim();

                          // Handle /allgallery
                          if (
                            trimmedHref === "/allgallery" &&
                            notification.post_id
                          ) {
                            e.preventDefault();
                            getGalleryDetail(notification.post_id);
                          }

                          // Handle /editprofile/viewrequest with redirect
                          if (trimmedHref === "/editprofile/viewrequest") {
                            e.preventDefault();
                            window.location.href = "/editprofile/viewrequest"; // or use navigate("/editprofile/viewrequest") if using useNavigate from react-router
                          }
                        }}
                        className="d-flex gap-3"
                        key={index}
                      >
                        <div className="flex-shrink-0">
                          <div className="iconmenu ofit rounded-circle overflow-hidden">
                            <img
                              src={
                                notification.status === "Admin"
                                  ? require("../assets/images/thumb.jpg")
                                  : notification.recipient_profile_image || pe1
                              }
                              alt="userpic"
                            />
                          </div>
                        </div>
                        <div className="flex-grow-1">
                          <p>
                            {notification.status === "User" && (
                              <strong>{notification.recipient_name} </strong>
                            )}
                            {notification.status === "Admin" && (
                              <strong>Admin </strong>
                            )}
                            {notification.message}
                          </p>
                          <p className="timeago">
                            {formateTime(notification.date)}
                          </p>
                        </div>
                      </Link>
                    ))}
                  </div>
                )}
                <div className="d-block morebtnnotification">
                  <button type="button" onClick={handleclicknotification}>
                    {t("seeprevious")}
                  </button>
                </div>
              </div>
            ) : (
              <div
                className="d-flex flex-column gap-3"
                style={{
                  maxHeight: "auto", // Auto height for 3 or fewer notifications
                  overflowY: "visible", // No scrolling needed
                }}
              >
                {notificationsdetail.length === 0 ? (
                  <p>{t("No_result_found")}</p>
                ) : (
                  <div className="d-flex flex-column gap-3 scroll_bar scrollyover">
                    {notificationsdetail.map((notification, index) => (
                      <Link
                        to={
                          notification.link_href?.trim() === "/previewimages" &&
                          notification.post_id &&
                          notification.image_url
                            ? `/previewimages/${
                                notification.post_id
                              }/${encodeURIComponent(notification.image_url)}`
                            : notification.link_href?.trim() || "#"
                        }
                        onClick={(e) => {
                          const trimmedHref = notification.link_href?.trim();

                          // Handle /allgallery
                          if (
                            trimmedHref === "/allgallery" &&
                            notification.post_id
                          ) {
                            e.preventDefault();
                            getGalleryDetail(notification.post_id);
                          }

                          // Handle /editprofile/viewrequest with redirect
                          if (trimmedHref === "/editprofile/viewrequest") {
                            e.preventDefault();
                            window.location.href = "/editprofile/viewrequest"; // or use navigate("/editprofile/viewrequest") if using useNavigate from react-router
                          }
                        }}
                        className="d-flex gap-3"
                        key={index}
                      >
                        <div className="flex-shrink-0">
                          <div className="iconmenu ofit rounded-circle overflow-hidden">
                            <img
                              src={
                                notification.status === "Admin"
                                  ? require("../assets/images/thumb.jpg")
                                  : notification.recipient_profile_image || pe1
                              }
                              alt="userpic"
                            />
                          </div>
                        </div>
                        <div className="flex-grow-1">
                          <p>
                            {notification.status === "User" && (
                              <strong>{notification.recipient_name} </strong>
                            )}
                            {notification.status === "Admin" && (
                              <strong>Admin </strong>
                            )}
                            {notification.message}
                          </p>
                          <p className="timeago">
                            {formateTime(notification.date)}
                          </p>
                        </div>
                      </Link>
                    ))}
                  </div>
                )}
                <div className="d-block morebtnnotification">
                  <button type="button" onClick={handleclicknotification}>
                    {t("seeprevious")}
                  </button>
                </div>
              </div>
            )}
          </div>
        </div>
      </div>
      <Usergallerynotification
        isOpen1={isPopupOpen1}
        onClose1={closePopup1}
        seperateGallerydetail={seperateGallerydetail}
        UserDetail={UserDetail}
        LoginData={LoginData}
        getUserfav={getUserfav}
        GalleryCommets={GalleryCommets}
        getCommentPost={getCommentPost}
        GalleryIdDetailGet={GalleryIdDetailGet}
        previousId={previousId}
        nextId={nextId}
        getgallerydetialnext={getgallerydetialnext}
        getgallerydetialprev={getgallerydetialprev}
      ></Usergallerynotification>
    </>
  );
}

export default Notification;
